.entity-dropdown-button {
  cursor: text;

  //TODO @SDV check if this or equivalent class exists
  &.mat-private-button-disabled {
    cursor: default;
  }
}

.eav-selection-suffix {
  margin-left: 8px;
  display: flex;

  .eav-selection-suffix-button {
    scale: calc(4 / 5);
    margin: 0px -4px;
  }

  .active {
    background-color: $accent-color;
    color: white;

    //TODO @SDV check if this or equivalent class exists
    &.mat-private-button-disabled {
      background-color: $editUI-label-disabled-color;
    }
  }
}

.eav-selection-suffix-inline {
  min-width: 80px;

  .mat-icon-button {
    top: 3px;

    .mat-icon {
      font-size: 18px;
    }
  }

  .active {
    background-color: $accent-color;
    color: white;

    &.mat-mdc-icon-button:disabled {
      background-color: $editUI-label-disabled-color;
      color: white;
    }
  }
}

